@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
/* - Dark Blue (Dark Mode Elements): hsl(209, 23%, 22%) - Very Dark Blue (Dark Mode Background): hsl(207, 26%, 17%) - Very Dark Blue (Light Mode Text): hsl(200, 15%, 8%) - Dark Gray (Light Mode Input): hsl(0, 0%, 52%) - Very Light Gray (Light Mode Background): hsl(0, 0%, 98%) - White (Dark Mode Text & Light Mode Elements): hsl(0, 0%, 100%) */

:root {
  --dark-blue: hsl(209, 23%, 22%);
  --very-dk-blue:hsl(207, 26%, 17%);
  --very-dk-blue-lg-mood: hsl(200, 15%, 8%);
  --dk-grey:hsl(0, 0%, 52%);
  --very-lg-gray:hsl(0, 0%, 98%);
  --white:hsl(0, 0%, 100%);
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: "Nunito Sans", sans-serif;
  font-size: 62.5%;
}

.container {
  max-width: 134rem;
  padding: 0 3rem;
  margin: 0 auto;
}

.flex {
  display: flex;
}
h1,h2,h3,h4,h5,h6{
  color: hsl(207, 26%, 17%) !important;
  color: var(--very-dk-blue) !important;
}
.description {
  font-size: 1.6rem;
  padding: 3px 0;
  color: hsl(209, 23%, 22%);
  color: var(--dark-blue);
 
}
.description .title{
  font-weight: 600;
  margin-right: 1rem;
  font-size: 2rem;
  color: hsl(207, 26%, 17%) !important;
  color: var(--very-dk-blue) !important;
}

/*  */
.btn {
  margin-top: 5rem;
  padding: 0.5rem 3rem 0.5rem 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
  cursor: pointer;
  border: none;
  box-shadow: inset 0px 0px 2px 2px rgba(0, 0, 0, 0.15);
  background-color: transparent;
  border-radius: 5px;
  font-size: 1.4rem;
}

.btn span {
  font-size: 2rem;
}
